@import "./base/sanitize.scss";
@import "./base/html5doctor.scss";
@import "./base/fonts.scss";
@import "./base/settings.scss";
@import "./common-blocks/header.scss";
@import "./media/mainPage-media.scss";

.promo {
    position: relative;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    padding-top: 100px;
    background: url('../assets/image/main_pic.jpg') no-repeat center / cover;
    height: 100vh;
    color: #fff;

    &::before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(#000, 0.7);
    }

    &__content {
        position: absolute;
        top: 50%;
        left: 12%;
        max-width: 300px;
        transform: translateY(-50%);
    }

    &__title {
        font-size: 27px;
        max-width: 504px;
        line-height: 131%;
        margin: 0;
        margin-bottom: 15px;
    }

    &__button {
        width: 130px;
        text-align: center;
        margin-left: 30px;
        background-color: #fff;
        padding: 14px 0;
        border-radius: 10px;
    }
}